
.skills-table
{
  width: 100%;
  border: none;
}

.skills-table tr
{
  background-color: #14232b;
  margin-bottom: 50px;
}

.skill-item
{
  background-color: white;
  color: black;
  margin: 10px;
  display: inline-flex;
  padding: 2px;

  border-radius: 5px;
}

.skills-title
{
  text-align: right;
  padding-right: 10px;
  padding-left: 40px;
}

.skills-table .space
{
  height: 10px;
}