
.timeline-table
{
  width: 100%;
  background-color: #14232b;
  border-collapse: collapse;
}


.timeline-table tr, .timeline-table td, .timeline-table th
{
  border: 4px solid #163341;
  border-collapse: collapse;

  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 5px;
  padding-right: 5px;
  
}

.timeline-table th
{
  color: white;
  background-color: #5c707a;
}

.timeline-table td b
{
  color: #e4b271;
}

.student-table tr:nth-child(odd)
{
  background-color: #101b25;
}


.student-table td:nth-of-type(3)
{
  font-weight: 1000;

  color: #e4b271;
}
.student-table td:nth-of-type(4)
{
  color: #95a5b1;
}

.student-table-with-year td:nth-of-type(4)
{
  font-weight: 1000;
  color: #e4b271;
}
.student-table-with-year td:nth-of-type(5)
{
  color: #95a5b1;
}

a
{
  color: #a6e2ff;
}

.education-table-wrapper h2
{
  display: inline-block;
}

.year-description
{

  float: right;
  color: #e4b271;
}